
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import Alert from 'vux/src/components/alert/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'


export default {
directives: {
TransferDom
},

components: {
XInput,
XButton,
Group,
Loading,
Alert
},

data () {
return {
account: '',
password: '',
isShowClear1: false,
isShowClear2: false,
isDisabled: true,
showLoading: false,
loadingText: 'loading',
showAlert: false,
alertTitle: '',
alertContent: ''
}
},

created(){
this.getUserInfo();
},
methods: {
checkValue(value){
if (this.account && this.password) {
this.isDisabled = false;
} else {
this.isDisabled = true;
}
},

login(){
this.showLoading = true;
this.$http.ajax({
url: apiHost + 'router_stat/auth/api/login',
dataType: 'json',
type: 'get',
context: this,
data: {
name: this.account,
password: this.password
}
}).done(res => {
this.showLoading = false;
if (res.result === 1) {
this.saveUserInfo();
this.$router.replace({name: 'home'});
} else if (res.result === 0) {
this.alertTitle = "请重新输入";
this.alertContent = "账号或密码错误";
this.showAlert = true;
}
}).fail(res => {
this.alertTitle = "网络错误";
this.alertContent = res;
this.showAlert = true;
})
},

saveUserInfo(){
client.setUserName(this.account);
client.setPassword(this.password);
},

getUserInfo(){
const account = client.getUserName();
const password = client.getPassword();
if (account && password) {
this.account = account;
this.password = password;
this.autoLogin(account, password);
}
},


autoLogin(account, password){
this.$http.ajax({
url: apiHost + 'router_stat/auth/api/login',
dataType: 'json',
type: 'get',
context: this,
data: {
name: account,
password: password
}
}).done(res => {
if (res.result === 1) {
this.saveUserInfo();
this.$router.replace({name: 'home'});
} else if (res.result === 0) {
this.alertTitle = "请重新输入";
this.alertContent = "账号或密码错误";
this.showAlert = true;
}
}).fail(res => {
this.alertTitle = "网络错误";
this.alertContent = res;
this.showAlert = true;
})
}

}
}



