
import XHeader from 'vux/src/components/x-header/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'


export default{
directives: {
TransferDom
},

components: {
XHeader,
},
data(){
return {
newList:[],
pageSize: 10,
pageNo: 1,
totalRows: null,
installList: [],
allLoaded: false,
isShowImg: false,
imgUrl: ''
}
},

created(){
this.getList();
this.installList = this.newList;
},
methods: {
getList(){
this.$http.ajax({
url: apiHost + "router_stat/api/router/list",
dataType: 'json',
type: 'POST',
async: false,
data: {
nextpage: JSON.stringify({
pageNo: this.pageNo,
pageSize: this.pageSize,
sortname: "log_time",
sortorder: "DESC"

})
},
context: this,
}).done(res => {
if (res.result === 1) {
this.totalRows = res.page.totalRows;
this.pageNo++;
this.newList = res.list;
}
}).fail(res => {

});
},

getImgUrl(item){
return apiHost + item;
},

getTime(timeStamp){
return new Date(parseInt(timeStamp) * 1000).toLocaleString();
},

loadBottom(){
this.getList();
setTimeout(() => {
this.installList.push(...this.newList);
if (this.installList.length === this.totalRows) {
this.allLoaded = true;
}
this.$refs.loadmore.onBottomLoaded();
}, 2500);
},


showImgDetail(img){
this.imgUrl = apiHost + img;
this.isShowImg = true;
},
}
}



